module.exports = "\n/* <------- Navigation Bar Styling ------->*/\n\n.scroll_main{\n  position: relative;\n  font-size: 30px;\n  padding-right: 10px;\n  top:15px;\n  color:white !important;\n  transition:.5s;\n}\n\n.head_text{\n  width:400px;\n  height: 60px;\n  margin:0 80px;\n  transition:.5s;\n  border-radius: 8px;\n}\n\n.head_text h1 {\n  font-size: 40px;\n  font-family: 'Nunito', sans-serif;\n  text-align: center;\n  border:2px solid white;\n  border-radius: 8px;\n  transition:.5s;\n}\n\n.head_text:hover h1{\n  color: rgb(107, 87, 124);\n  border:2px solid rgb(107, 87, 124);;\n}\n\n.head_text:hover i{\n  color: rgb(107, 87, 124) !important;\n}\n\n.nav_bar{\n  width:100%;\n  display: flex;\n  position: fixed;\n  top:0;\n  padding-top: 10px;\n  padding-bottom: 10px;\n  z-index: 1;\n  background-color: hsl(184, 18%, 30%);\n}\n\n.nav_bar ul {\n  position: fixed;\n  margin:10px 200px;\n}\n\n/*------->> Search */\n\n.fa-search{\n  color:white;\n  font-size: 18px;\n  padding-top: 12px;\n  padding-left: 20px;\n}\n\n.search{\n  position: fixed;\n  margin-top: 10px;\n  right:80px;\n  background-color: rgba(0, 0, 0, 0.171);\n  border-radius: 10px;\n}\n\n.search input{\n  width:400px;\n  border:none;\n  height:40px;\n  font-size: 16px;\n  color:white;\n  font-weight: 100;\n  padding-left: 15px;\n  background-color: rgba(255, 255, 255, 0);\n}\n\n.search input:focus{\n  border:none;\n  outline: none;\n}\n\n::-webkit-input-placeholder { /* Chrome/Opera/Safari */\n  color:rgba(255, 255, 255, 0.884);\n}\n\n::-moz-placeholder { /* Firefox 19+ */\n  color:rgba(255, 255, 255, 0.884);\n}\n\n:-ms-input-placeholder { /* IE 10+ */\n  color:rgba(255, 255, 255, 0.884);\n}\n\n:-moz-placeholder { /* Firefox 18- */\n  color:rgba(255, 255, 255, 0.884);\n}\n\n/*------->> Login/Register */\n\n.nav_bar ul li{\n  width:40px;\n  height:40px;\n  text-align: center;\n  display: inline-block;\n  padding-top: 6px;\n  border-radius: 10px;\n  margin:0 20px 0 20px;\n  cursor: pointer;\n  transition: .5s;\n}\n\n.nav_bar ul li i{\n  text-decoration: none;\n  font-size: 30px;\n  text-align: center;\n  transition: .5s;\n}\n\n.about{\n  background-color: white !important;\n  border:none !important;\n  padding-top: 8px !important;\n  transition: .5s;\n}\n\n.about a {\n  color:hsl(184, 24%, 55%) !important;\n}\n\n.nav_bar ul li:hover i {\n  color:rgb(92, 56, 109);\n}\n\n/* <------- Add_post ------->*/\n\n.add_post{\n  display: inline-block;\n  width: 52px;\n  margin:auto 20px;\n  height:60px;\n  transition: .5s;\n}\n\n.add_post i{\n  font-size: 50px;\n  color: rgba(255, 255, 255, 0.322);\n  transition: .5s;\n}\n\n.add_post i:hover{\n  color:rgb(219, 219, 219);\n}\n\n.add_post_text {\n  margin:auto;\n  padding:15px 0 0 24px;\n  display: inline-block;\n  position:relative;\n  color:white;\n  font-size: 20px;\n  visibility: hidden;\n}\n\n#post_form{\n  display:block;\n  margin:20px auto;\n  width:460px;\n  min-height:100px;\n  border-radius: 6px;\n  background-color: white;\n}\n\n#post_form i{\n  font-size: 50px;\n  width:80px;\n  margin-top: 10px;\n  margin:10px 6px;\n  height:80px;\n  padding:16px 14px;\n  transition: .5s;\n}\n\n#post_form i:hover{\n  color: hsl(130, 36%, 50%);\n}\n\n.fa-link{\n  color:rgb(57, 76, 116);\n}\n\n.fa-images{\n  color:rgb(50, 100, 50);\n}\n\n.fa-font{\n  color:rgb(34, 34, 34);\n}\n\n.fa-youtube{\n  color:rgb(185, 42, 42);\n}\n\n.fa-headphones{\n  color:rgb(79, 49, 99);\n}\n\n/* <------- Blog Styling ------->*/\n\n.main_dash{\n  position: relative;\n  display: block;\n  width:36%;\n  margin:100px auto;\n  min-height: 760px;\n  border-radius: 6px;\n  padding:20px 0 60px 0;\n  background-color: rgba(0, 0, 0, 0.171);\n}\n\n.blog_contain{\n  width:100%;\n  margin:auto;\n}\n\n.blog_card{\n  display:block;\n  margin:20px auto;\n  width:460px;\n  min-height:350px;\n  border-radius: 6px;\n  background-color: white;\n}\n\n.blog_card h3{\n  font-size: 16px;\n  text-align: left;\n  margin-top: 30px;\n  padding-left: 0px;\n  color:black;\n  font-weight: bold;\n  display: inline-block;\n}\n\n.blog_card span{\n  margin:0;\n  padding:0;\n  top:0;\n  color:grey;\n}\n\n.card_img{\n  background-color: rgb(17, 17, 17);\n}\n\n.img{\n  width:100%;\n  margin:auto;\n}\n\n.gif{\n  width:100%!important;\n}\n\n.post_profile_photo{\n  width:50px!important;\n  border-radius: 50%;\n  display: inline-block;\n  margin:15px;\n}\n\n.card_contents{\n  width:80%;\n  margin:auto;\n}\n\n.card_contents h2, p{\n  color:rgb(24, 24, 24);\n}\n\n.card_contents h2{\n  padding: 10px 0;\n  text-align: center;\n  font-weight: bold;\n}\n\n.card_contents p{\n  padding-bottom: 5px;\n}\n\n.card_footer{\n  width:100%;\n  position: relative;\n  bottom: -5px;\n  background-color: rgb(255, 255, 255);\n  height:50px;\n  border-bottom-left-radius: 6px;\n  border-bottom-right-radius: 6px;\n  text-align: right;\n}\n\n.card_footer .fa-scroll{\n  color:rgb(109, 109, 109) !important;\n  display: inline-block !important;\n\n}\n\n.card_footer p{\n  display: inline-block;\n  color:grey;\n  padding-right: 220px;\n  padding-top: 10px;\n}\n\n.card_footer i{\n  color:rgb(109, 109, 109);\n  position: relative;\n  padding-top: 15px;\n  display: inline-block;\n  font-size: 24px;\n  margin: 0 10px;\n  transition: .5s;\n}\n\n.card_footer i:hover{\n  color:rgb(97, 158, 95)!important;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"